import React from 'react';
import { Route, Redirect } from 'react-router-dom';

class AdminPanel extends React.Component {

  state = {
    isAdminLogged: false,
  }

  render() {
    return (
      <Route render={() => (
        this.state.isAdminLogged ? <p>Panel admina - witamy</p> : <Redirect to="/login" />
      )} />
    )
  }
}

export default AdminPanel;