import React from 'react';
import '../styles/Page.css';
import { Prompt } from 'react-router-dom';
class ContactPanel extends React.Component {

  state = {
    contactMessage: '',
    isSent: false,
  }

  handleTyping = (e) => {
    this.setState({
      contactMessage: e.target.value,
    })
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.setState({
      contactMessage: '',
      isSent: true,
    })

  }

  render() {
    return (
      <div className="contact">
        <h2>Napisz do nas!</h2>
        <form onSubmit={this.handleSubmit}>
          <textarea placeholder="Wpisz wiadomość..." rows="5" cols="40" value={this.state.contactMessage} onChange={this.handleTyping}></textarea>
          <button>Wyślij</button>
          {this.state.isSent ? <p>Poprawnie wysłano formularz</p> : null}
        </form>
        <Prompt
          when={this.state.contactMessage}
          message="Czy na pewno chcesz wyjść? Zmiany nie zostaną zapisane"
        />
      </div>
    )
  }
}

export default ContactPanel;