import React from 'react';
import { Route, Switch } from 'react-router-dom';
import '../styles/Header.css';
import img1 from '../images/header1.jpg';
import img2 from '../images/header2.jpg';
import img3 from '../images/header3.jpg';
import imgError from '../images/error.jpg';
const Header = () => {
  return (
    <>
      <Switch>
        <Route path="/" exact render={() => <img src={img1} alt="landscape"></img>}></Route>
        <Route path="/products" render={() => <img className="img2" src={img2} alt="landscape"></img>}></Route>
        <Route path="/contact" render={() => <img className="img3" src={img3} alt="landscape"></img>}></Route>
        <Route path="/adminPanel" render={() => <img src={img1} alt="landscape"></img>}></Route>
        <Route render={() => <img className="headerErr" src={imgError} alt="Error"></img>}></Route>
      </Switch>
    </>
  )
}

export default Header;